@charset "UTF-8";

html {
    position: relative;
    min-height: 100%;
    scroll-behavior: smooth;
    overflow: auto;
}

body {
    font-family: 'Lato', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: hidden;
    color: #fff;
}

@supports (-webkit-overflow-scrolling: touch) {
    body {
        cursor: pointer;
    }
}

.logo {
    display: flex;
    line-height: 55px;
    width: 99px;
}

.wrapper {
    height: 100%;
    overflow: hidden;
    width: 100%;
}

.content-page {
    margin-left: 0px;
    overflow: hidden;
    padding: 55px 0 0 0;
    min-height: 100vh;
}

.leftside-menu {
    width: 90px;
    z-index: 10;
    /* background: linear-gradient(135deg, #8f75da 0, #727cf5 60%); */
    bottom: 0;
    position: fixed;
    top: 55px;
    /* margin-top: 55px; */
    /* -webkit-box-shadow: 0 0 35px 0 rgba(154, 161, 171, 0.15);
    box-shadow: 0 0 35px 0 rgba(154, 161, 171, 0.15); */
}

.leftside-menu.show {
    display: block;
}

.side-nav {
    padding-left: 0;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.side-nav ul {
    list-style-type: none;
}

.side-nav .side-nav-link {
    /* color: #cedce4; */
    display: flex;
    align-items: center;
    margin: 4px 0px;
    /* font-size: 0.9375rem; */
    position: relative;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    list-style: none;
    width: 40px;
    height: 40px;
}

.side-nav-circle {
    width: 40px;
    height: 40px;
    background: #0379ae;
    border-radius: 100%;
    display: flex;
    opacity: 1;
}

.side-nav .side-nav-item {
    /* margin-bottom: 8px; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.side-nav-text {
    display: none;
    align-items: center;
    color: #fff;
    text-transform: uppercase;
    font-size: 20px;
    margin-bottom: 0;
    position: absolute;
    top: -24px;
    left: 25px;
    width: 260px;
}
.side-nav .side-nav-item .side-nav-link.side-nav-link-hover .side-nav-text {top: -45px;}
.side-nav-text .side-img-icon {
    width: 90px;
    height: 90px;
    border-radius: 25px;
    background-color: #0379ae;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
}

/* .side-nav .side-nav-item.active {
    background-color: #fff;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
} */
.side-nav .side-nav-item.active img {
    filter: unset;
}
.side-nav .side-nav-item .side-nav-link:hover {height: 60px;margin-top:20px}
.side-nav .side-nav-item .side-nav-link.side-nav-link-hover:hover {height: auto}
.side-nav .side-nav-item .side-nav-link:hover .side-nav-circle {
    /* display: none; */opacity: 0;
}

.side-nav .side-nav-item .side-nav-link:hover .side-nav-text {
    display: flex;
}

.side-nav .side-nav-item img {
    filter: brightness(0) invert(1);
}

.side-nav .side-nav-link:active,
.side-nav .side-nav-link:focus,
.side-nav .side-nav-link:hover {
    color: #fff;
    text-decoration: none;
}

.side-nav .menu-arrow {
    -webkit-transition: -webkit-transform 0.15s;
    transition: -webkit-transform 0.15s;
    transition: transform 0.15s;
    transition: transform 0.15s, -webkit-transform 0.15s;
    position: absolute;
    right: 30px;
    display: inline-block;
    font-family: "Material Design Icons";
    text-rendering: auto;
    line-height: 1.5rem;
    font-size: 1.1rem;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
}

.side-nav .menu-arrow:before {
    content: "\F0142";
}

.side-nav .badge {
    margin-top: 3px;
}

.side-nav .side-nav-item>a[aria-expanded="true"]>span.menu-arrow {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}

.side-nav .side-nav-item.menuitem-active>a:not(.collapsed)>span.menu-arrow {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}

.side-nav .side-nav-title {
    padding: 12px 30px;
    letter-spacing: 0.05em;
    pointer-events: none;
    cursor: default;
    font-size: 0.6875rem;
    text-transform: uppercase;
    color: #cedce4;
    font-weight: 700;
}

.side-nav .menuitem-active>a {
    color: #fff !important;
}

[dir="ltr"] .side-nav .side-nav-item .menu-arrow:before {
    content: "\F0141" !important;
}

.side-nav-forth-level,
.side-nav-second-level,
.side-nav-third-level {
    padding-left: 0;
}

.side-nav-forth-level li a,
.side-nav-second-level li a,
.side-nav-third-level li a {
    padding: 8px 30px 8px 70px;
    color: #cedce4;
    display: block;
    position: relative;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    font-size: 0.89rem;
}

.side-nav-forth-level li a:focus,
.side-nav-forth-level li a:hover,
.side-nav-second-level li a:focus,
.side-nav-second-level li a:hover,
.side-nav-third-level li a:focus,
.side-nav-third-level li a:hover {
    color: #fff;
}

.side-nav-forth-level li a .menu-arrow,
.side-nav-second-level li a .menu-arrow,
.side-nav-third-level li a .menu-arrow {
    line-height: 1.3rem;
}

.side-nav-forth-level li.active>a,
.side-nav-second-level li.active>a,
.side-nav-third-level li.active>a {
    color: #fff;
}

.side-nav-third-level li a {
    padding: 8px 30px 8px 80px;
}

.side-nav-forth-level li a {
    padding: 8px 30px 8px 100px;
}

